
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import bheader from "@/components/header/header-b";
import circleTitle from './../../../components/circleTitle/circleTitle'
export default {
name: "withdrawal",
data() {
return {
tixian:'',
getinfo:'',
btnloading:false,
btnWord:'确 认',
}

},
components: {
bheader, circleTitle, Box, XButton
},
created() {
},
mounted() {
this.getlist();
},
watch:{
tixian(nwl,old){
this.tixian=(nwl>parseFloat(this.getinfo.user_money))?parseFloat(this.getinfo.user_money):nwl
}
},
methods: {
getlist(){
let   _this=this;
this.$axios.post('/api/Myprofit/withdraw','').then(
function(res){

if(res.code==1){
_this.getinfo=res.result;
if(res.result.bankcard_id=="null" || res.result.bankcard_id==null){
_this.$vux.confirm.show({
title: '您还未绑定银行卡，立即前往',
onCancel () {
},
onConfirm () {
_this.$router.push({name:'debitCard'});
}
})
}
}else{
mui.toast(res.msg);
}
}
)
},
confirm(){
let   _this=this;
this.btnloading=true;
this.btnWord='提现中...';
this.$axios.post('/api/Myprofit/do_withdraw',`data=`+JSON.stringify({withdraw_money: _this.tixian,bankcard_id: _this.getinfo.bankcard_id}) ).then(
function(res){

this.btnloading=false;
this.btnWord='确 认';
if(res.code==1){
mui.toast(res.msg);
_this.tixian='';
_this.getlist();

}else{
mui.toast(res.msg);
}
}
)
}
}
}



